.th-option-area {
    margin: 25px 0px 25px 0px;
    display: flex;
    flex-direction: column;
    position: relative;
    background: #2c3e6e;
    border-radius: 2px;
    border: 1px solid #f4f2f2;
    box-shadow: 0px 0px 4px 0px #dcf5ff;
    box-shadow: 2px 2px 3px 0px #e4e4e6;
}
.th-option-top-hdr{
    display: flex;
    align-items: center;
    position: relative;
}

.th-option-top-hdr .th-col1 {
    width: 100px;
    padding: 1rem;
}
.th-option-top-hdr .th-col1 .logo-image{
display: block;
}

.th-option-top-hdr .th-col2 {
    padding: 1rem;
    display: flex;
    align-items: center;
}
.th-option-top-hdr .th-col1 img{
max-width:100%;
}

.th-option-heading h2 {
    font-size: 21px;
    font-weight: bold;
    line-height: 10px;
    color: #fff;
}

.th-option-heading p {
    position: relative;
    font-size: 15px;
    color:#d7d5d5;
}

.th-option-detail{
   margin-left: 100px;
    display: flex;
    align-items: center;
}
.th-option-detail p {
    display: block;
    font-weight: bold;
    font-size: 20px;
    position: relative;
    color: #d7d5d5;
}
.th-option-area .th-version:after {
    border: 1px solid #999;
    content: " ";
    margin: 0 9px;
}
.th-option-area span {
    color: #f98a17;
    font-weight: 700;

}
.appearance_page_blur_thunk_started #wpcontent{
    padding:0;
    background:#fff;
    }
    
    #blur-theme-setting-page{
    padding:1rem;
    }
    
    .blur-theme-options-main{
        width: 100%;
        display: flex;
    }
    
    .option-tab-content-wrap {
        width: 70%;
        box-shadow: 0px 0px 4px 0px #dcf5ff;
    }
    
    .option-sidebar-wrap {
        width: 25%;
        margin-left:3rem;
        padding: 1.3rem;
        box-shadow: 0px 0px 4px 0px #dcf5ff;
        background:#f8faff;
    }
    .option-sidebar-wrap p {
        font-weight: 400;
        line-height: 1.6;
        font-size:0.90rem;
    }
    .option-sidebar-wrap h3 {
        color: #111;
        font-size: 1.3em;
        margin: 1em 0;
    }
    .blur-tabs button {
        padding: 1rem 1.5rem;
        color: #111;
        font-size:1rem;
        font-weight: bold;
        cursor: pointer;
        position: relative;
        text-decoration: none;
        background: #f8faff;
         border:0;
    }

    .blur-tabs{
        background: #f8faff;
    }
    
    .blur-tabs button.active {
        color: #fff;
        background: #225992;
    }  

    .blur-options-tab .tab-content{
        padding:1rem;
    }

    .option-content-wrp {
        margin-top:1.5rem;
        flex-direction: column;
        justify-content: flex-start;
        height: 815px;
    }
    .option-content-wrp {
        display: flex;
    flex-wrap: wrap;
    grid-column-gap: 1rem;
    grid-row-gap: 1.5rem;
    }
    
    .tab-content .content-box {
        background: #fff;
        width: 48.5%;
        padding: 1.3rem;
        box-sizing: border-box;
        box-shadow: 0px 0px 4px 0px #dcf5ff;
    }
    .option-content-wrp .content-box h3{
        font-size: 1.1rem;
    }
    .option-content-wrp .content-box p{
        line-height: 1.5;
        font-size: 1rem;
    }
    .sidebar-option-content-wrp hr {
        margin: 1.5rem 0;
    } 
    .th-option-2-col{
        display: flex;
        flex-wrap: wrap;
        grid-column-gap: 20px;
        grid-row-gap: 45px;

    }

    .th-option-row {
        display: flex;
    }
    .th-col{
        flex: 50%;
    }
    .title-plugin h4 {
        font-size: 16px;
        margin: 15px 0;
    }

    .title-plugin{
    margin-bottom:1rem;
    }

    .button.btn-active-now {
        background: #2271b1;
        border-color: #2271b1;
        color: #fff;
        text-decoration: none;
        text-shadow: none;
    }
    
    .button.btn-active-now:focus, .button.btn-active-now:hover {
        background: #135e96;
        border-color: #135e96;
        color: #fff;
    }

    .button.btn-activated.disabled {
        pointer-events: none;
       }

    .th-col img {
        border: none;
        width: 128px;
    }